.search {
  width: 100%;
  display: block;
  font-family: "Bebas Neue", helvetica, arial, sans-serif;

  padding: {
    top: 0.5em;
    bottom: 0.5em;
    left: 2.95em;
    right: 0.75em;
  }

  color: #333;
  font-size: 1.5em;
  font-weight: 700;

  border: none;
  border-radius: 0.5em;
  background: white;
  box-sizing: border-box;

  outline: none;
  appearance: none;

  background: {
    image: url("../../img/search.svg");
    repeat: no-repeat;
    position-y: 50%;
    position-x: 1em;
    color: white;
    size: 1em;
  }
}

@media screen and (max-width: 520px) {
  .search {
    font-size: 1.15em;
  }
}
